/* App.css */
.App {
  text-align: center;
}

.top-nav {
  background-color: #333;
  color: #fff;
  padding: 10px 0;
}

.nav-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-list li {
  display: inline-block;
  margin-right: 20px;
}

.nav-list li a {
  text-decoration: none;
  color: #fff;
}
.nav-links ul li a::after {
  content: "";
  width: 0%;
  height: 2px;
  background: #a45693;
  display: block;
  margin: auto;
  transition: 0.5s;
}
.nav-links ul li a:hover::after {
  width: 100%;
}
.nav-links li a::after {
  content: "";
  width: 0%;
  height: 2px;
  background: #ffffff;
  display: block;
  margin: auto;
  transition: 0.5s;
}
.nav-links li a:hover::after {
  width: 100%;
}
@media screen and (max-width: 1024px) {
  .products,
  .useful {
    display: none;
  }
}
@media screen and (max-width: 768px) {
  .profile {
    display: none;
  }
}
.btn {
  background-color: transparent;
  background-image: linear-gradient(to right, #5961f9 0%, #ee9ae5 100%);
}
